body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #002B36;
    border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #002B36;
}

/* width */
::-moz-scrollbar {
    width: 10px;
}

/* Track */
::-moz-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-moz-scrollbar-thumb {
    background: #002B36;
    border-radius: 50px;
}

/* Handle on hover */
::-moz-scrollbar-thumb:hover {
    background: #002B36;
}

@media screen and (max-width: 576px) {

  div.jumbotron{
    flex: 0 0 100%;
  }
  .sidebar{
    display: none;
  }
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  width: 100vw;
  height: 100vh;
}
body{
  width: 100%;
  height: 100%;
}
#root{
  width: 100%;
  height: 100%;
}
div.wrapper{
  background-image: url('/background.jpg');
  background-repeat: no-repeat;
    background-position: top center;
    background-attachment: fixed;
    background-origin: content-box;
    background-size: cover;
    opacity: 0.6;
    
}
div.app-wrapper{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

button.dashboard{
  float: right;
  background-color: red;
  padding: 10px;
  border: none;
  border-radius: 50px;
  color: white;
  cursor: pointer;
  box-shadow: 0 4px 4px 0px rgba(0,0,0,0.5);
}
div.wrap{
  margin: auto 0;
  text-align: center;
}
h1.head{
  color: white;
  background-color: #006677;
}
h1.title{
  
  background-color: #002B36;
  color: white;
  width: 50%;
  margin: 0 auto;
  padding: 10px;
  font-weight: bolder;
  font-size: 15px;
  font-family: monospace;
}
header.app-header{
  --theme-color: #003366;
  background-color: var(--theme-color);
  color: #f1f1f1;
  height: 8%;
  padding: 10px;
}
header.app-header img.logo{
  width: 40px;
  height: 40px;
}
header.app-header span.name{
  font-size: 14px;
  font-weight: bold;
  font-family: cursive;
  color: white;
}
span{
	padding: 5px;
}


.notification{
	position: fixed !important;
	height: 50% !important;
	overflow: auto;
	top: 20% !important;
	left: 35% !important;
	width: 50% !important;
	color: var(--theme-color) !important;
}
.notification-heading{
	background-color: var(--theme-color) !important;
	color: white !important;
	padding: 5px;
}
.close-notification{
	color: white !important;
}
footer.app-footer{
  background-color: #003366;
  color: #f1f1f1;
  height: 6%;
  padding: 10px;
  font-size: 12px;
}

main.info{
  width: 100%;
  height: 84%;
  background-color: #5ACDB6;
  padding: 0 !important;
 
}
main.info div.jumbotron{
  background-color: #5ACDB6;
  text-align: center;
  color: white;
  width: 100%;
  margin: 0 !important;
  height: 100%;
}

main.info div.jumbotron h1.display-3{
	font-size: 3rem !important;
}

.settings-btn{
  background-color: #DD4B39 !important;
  color: white !important;
  box-shadow: 0px 4px 4px grey;
}

.rounded-box{
  width: 100px;
  height: 100px;
  border-radius: 100%;
  background: #697581;
  color: white;
  padding: 10px;
  margin: auto;
  text-align: center;
  font-family: cursive;
  font-size: 20px;
  font-weight: bold;
  font-style: oblique;
  margin-bottom: 10px;
}

.rounded-box span{
  font-weight: bold;
  color: #f44259 !important;
}

form.admin-form div.rounded{
  width: 80px;
  height: 80px;
  margin: 0 auto;
  border-width: 8px;
  border-style: ridge;
  border-color: #252831 #DD4B39 transparent #DD4B39;
  border-radius: 50% !important;
  text-align: center;
  outline: none;
  font-size: 35px;
  font-family: arial;
  color: #003366;
  margin-bottom: 8px;
  padding: 5px;
}

div.sidebar{
  flex: 0 0 20%;
  height: 100%;
  background-color: #222D32;
  color: #FCF4F6;

}

div.sidebar div.dashboard-list a{
  background-color: transparent !important;
  color: #E8EAED;
  cursor: pointer;
}

input.search, input.search:focus{
  background-color: #374850;
  border: none;
  color: #FCF4F6;
  box-shadow: none;
}

input.search::-webkit-input-placeholder{
  font-style: italic;
}

input.search::-moz-placeholder{
  font-style: italic;
}

input.search::-ms-input-placeholder{
  font-style: italic;
}

input.search, input.search::placeholder{
  font-style: italic;
}

span.icon{
  float: right;
}

