body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #002B36;
    border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #002B36;
}

/* width */
::-moz-scrollbar {
    width: 10px;
}

/* Track */
::-moz-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-moz-scrollbar-thumb {
    background: #002B36;
    border-radius: 50px;
}

/* Handle on hover */
::-moz-scrollbar-thumb:hover {
    background: #002B36;
}

@media screen and (max-width: 576px) {

  div.jumbotron{
    flex: 0 0  100%;
  }
  .sidebar{
    display: none;
  }
}